.titleing{
  position: absolute;
  bottom: 20px;
  padding: 1rem;
  margin: 0;
  width: 100%;
  background-color: gray;
  background-color: rgba(99, 99, 99, 0.892);
  color: white;
  box-sizing: border-box;
  height: 20%;
  text-align: center;
}

h3{
  position: absolute;
  bottom: 20px;
  padding: 1rem;
  margin: 0;
  width: 100%;
  background-color: gray;
  background-color: rgba(99, 99, 99, 0.892);
  color: white;
  box-sizing: border-box;
  height: 20%;
  text-align: center;
}
.ListItem {

  .list{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding-inline-start: 0;
      }

  & .rating {
      position: absolute;
      top: -16px;
      left: 16px;
      display: block;
      width: 32px;
      height: 32px;
      border-radius: 50%;

      background: rgba(0, 0, 0, 1);
      color: rgba(255, 255, 255, 1);

      text-align: center;
      line-height: 32px;
      font-size: 0.8rem;
  }
}
.meta {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  top: 0;
  border-top: 1px solid #e3e3e3;
  padding: 8px 16px;
  box-sizing: border-box;
}

.list_item{
  max-width: 100%;
  margin: 0.5rem;
  box-sizing: border-box;
  position: relative;
  transition: 0.125s ease;
  list-style: none;
  height: 100%;
  &.link{
      position: relative;
      
  }
  }
  .wrapper{
    max-width: 1950px;
    margin: auto;
  }
  .img{
    max-width: 100%;
  }